import React from "react";
import product from "../database/product.json";
import Price from "./PriceCard";

const Card = ({ type }) => {
  // filter data produk sesuai tab dan di simpan di variable baru sebagai array of object yg sudah terfilter sesuai type yang di kirim dari tabs
  const filteredProducts =
    type != null ? product.filter((data) => data.type.includes(type)) : product;

  return (
    <div className="wrapcard">
      {filteredProducts.map((data, i) => (
        <div className="card" key={i}>
          <img
            src={require(`../asset/images/${data.url}`)}
            alt={data.alt}
            width={data.width}
            height={data.height}
          />
          <div className="card-text">
            <h3>{data.title}</h3>
            <p>{data.ket}</p>
            <Price price={data.price} />
          </div>
        </div>
      ))}
    </div>
  );
};

export default Card;
