import * as React from "react";
import * as ReactDOM from "react-dom/client";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import "./index.css";
import Cart from "./pages/Cart";
import App from "./pages/App";
import ErrorPage from "./pages/ErrorPage";
import Info from "./pages/Info";
import History from "./pages/History";
import Contact from "./pages/Contact";

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <Router>
      <Routes>
        <Route path="/belajar-reactapp/" element={<App />} />
        <Route path="/belajar-reactapp/cart" element={<Cart />} />
        <Route path="/belajar-reactapp/about" element={<Info />} />
        <Route path="/belajar-reactapp/email" element={<Contact />} />
        <Route path="/belajar-reactapp/history" element={<History />} />
        <Route path="*" element={<ErrorPage />} />
      </Routes>
    </Router>
  </React.StrictMode>
);
