const Tablecart = () => {
  return (
    <>
      <div className="overflow-x-auto w-3/5">
        <table className="table text-slate-300 w-full">
          {/* head */}
          <thead>
            <tr className="text-white">
              <th></th>
              <th>Product</th>
              <th>Quantity</th>
              <th>Price</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {/* row 1 */}
            <tr>
              <th>
                <label>
                  <input type="checkbox" className="checkbox" />
                </label>
              </th>
              <td>
                <div className="flex items-center space-x-3">
                  <div className="avatar">
                    <div className="mask mask-squircle w-12 h-12">
                      <img
                        src={require("../../asset/images/inoo.png")}
                        alt="Avatar Tailwind CSS Component"
                      />
                    </div>
                  </div>
                  <div>
                    <h3 className=" text-base font-bold">Hart Hagerty</h3>
                    <p className="text-medium">
                      ukuran : <span className="text-sm">M</span>
                    </p>
                  </div>
                </div>
              </td>
              <td>
                <span className="badge badge-ghost badge-sm ml-2">1</span>
              </td>
              <td>Rp. 10.000.00</td>
              <th>
                <button className="btn btn-ghost btn-xs text-secondary">
                  details
                </button>
              </th>
            </tr>
            {/* row 2 */}
          </tbody>
        </table>
      </div>
    </>
  );
};

export default Tablecart;
