import { Link } from "react-router-dom";
import Drawer from "../components/Drawer";

const Info = () => {
  return (
    <div>
      <Drawer />
      <div className="hero min-h-screen bg-neutral">
        <div className="hero-content flex-col lg:flex-row">
          <img
            src={require("../asset/images/home.jpg")}
            className="max-w-sm rounded-lg shadow-2xl"
            alt="home"
          />
          <div>
            <h1 className="text-5xl font-bold text-info">About Think!</h1>
            <p className="pt-6 text-white text-base">
              Toko fashion "Think" adalah sebuah butik modern yang berfokus pada
              pakaian dan aksesori trendi. Toko ini didirikan pada tahun 2022
              oleh Gwe, seorang desainer fashion yang berbakat dan
              berpengalaman.
            </p>
            <p className="py-6 text-slate-100 text-base">
              Think didesain dengan konsep yang elegan dan nyaman, menciptakan
              suasana yang menyenangkan bagi para pelanggan yang mencari pakaian
              yang stylish dan terkini. Toko ini menyediakan berbagai koleksi
              pakaian, mulai dari busana sehari-hari yang santai hingga pakaian
              formal yang memukau.
            </p>
            <button className="btn btn-primary">
              <Link to={"/belajar-reactapp/"}> Back to Home </Link>{" "}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Info;
